<template>
  <div>
    <bread-nav :nav="nav"/>

    <div class="card">
      <div class="section">
        <h1 class="section__title">标题</h1>
        <el-input v-model="submit.title"
                  placeholder="标题"/>
      </div>
      <div class="section">
        <h1 class="section__title">正文内容</h1>
        <wang-editor :content.sync="submit.content"/>
      </div>

      <div class="section">
        <h1 class="section__title">题目</h1>
        <div v-for="(question, questionIdx) in submit.questionList"
             :key="questionIdx"
             class="section__content bg-gray">
          <h1 class="section__title">
            <span>题目{{questionIdx + 1}}</span>
            <span class="section__subtitle">{{$enums.SurveyQuestionType.getName(question.questionType)}}</span>
            <span class="section__btn section__btn--delete"
                  v-show="submit.questionList.length > 1"
                  @click="doDeleteQuestion(questionIdx)">删除题目</span>
          </h1>
          <el-input placeholder="此处填写题目内容"
                    v-model="question.title"/>

          <div class="section"
               v-if="question.optionList.length">
            <h1 class="section__title">
              <span>选项</span>
              <span class="section__subtitle">单/多选题的选项范围从2至20</span>
              <span class="section__btn"
                    @click="doAddOption(questionIdx)">添加选项</span>
            </h1>

            <div v-for="(option, optionIdx) in question.optionList"
                 :key="optionIdx"
                 class="section__question">
              <span class="el-radio">{{String.fromCharCode(optionIdx + 65) + '、'}}</span>
              <el-input v-model="question.optionList[optionIdx]"/>
              <i class="el-icon-delete"
                 v-show="optionIdx > 1"
                 @click="doDeleteOption(questionIdx, optionIdx)"></i>
            </div>
          </div>
        </div>

        <el-dropdown @command="doAddQuestion">
          <span class="btn-submit"
                type="primary">
            添加问题<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(item, idx) in $enums.SurveyQuestionType.list"
                              :key="idx"
                              :command="item.value">
              {{item.label}}
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>

      <div class="section">
        <h1 class="section__title">截止时间</h1>
        <el-date-picker v-model="submit.endTime"
                        type="date"
                        :picker-options="pickerOptions"
                        class="fixed-width"
                        value-format="yyyy-MM-dd 00:00:00"/>
      </div>

      <div class="section">
        <h1 class="section__title">推送范围</h1>
        <el-input disabled
                  class="fixed-width"
                  v-model="selectedDepName.join()"
                  placeholder="请选择推送范围">
          <span @click="depDialog = true" class="btn-submit" slot="append">选择</span>
        </el-input>
      </div>

      <div class="btn-group">
        <span class="btn-submit" @click="doSubmit">确定</span>
      </div>
    </div>
    <el-dialog :visible.sync="depDialog" class="dialog" width="400px">
      <div class="dep-form">
        <p class="title">{{depTitle}}</p>
        <div class="content">
          <el-tree :data="depData"
                   :props="props"
                   show-checkbox
                   node-key="orgId"
                   :default-checked-keys="submit.orgIdList"
                   @check-change="handleCheckChange">
          </el-tree>
        </div>
        <div class="btns">
          <div @click="depDialog = false" class="btn-submit">关闭</div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import item from '../../school/user/Item'

  export default {
    components: {
      item
    },
    data () {
      return {
        nav: [{name: '调查问卷', path: 'go(-1)'}, {name: '发布', path: ''}],
        pickerOptions: {
          disabledDate (time) {
            return time.getTime() < Date.now()
          }
        },
        depDialog: false,
        depTitle: '家长组',
        // 部门树
        depData: [],
        // 选择的部门ID
        selectedDep: [],
        // 选择的部门名称
        selectedDepName: [],
        // 需要提交的数据模型
        submit: {
          title: null,
          content: null,
          endTime: new Date(new Date().getTime() + 24 * 60 * 60 * 1000).$Format('yyyy-MM-dd 00:00:00'),
          orgIdList: [],
          questionList: []
        },
        props: {
          label: 'orgName',
          children: 'subOrgList'
        },
      }
    },
    async mounted () {
      this.submit.questionList.push(this.createQuestion(this.$enums.SurveyQuestionType.list[0].value))
      let data = await this.$ajax({
        url: '/org/queryOrganizationTree.do',
        query: {
          orgTypes: ['PARENT_ORG']
        }
      })
      this.depData = this.recursion(data.subOrgList, 1)
    },
    methods: {
      handleCheckChange (data, checked, indeterminate) {
        let index = this.submit.orgIdList.indexOf(data.orgId)
        if (checked) {
          this.submit.orgIdList.push(data.orgId)
          this.selectedDepName.push(data.orgName)
        } else {
          this.submit.orgIdList.splice(index, 1)
          this.selectedDepName.splice(index, 1)
        }
      },
      // 递归给每一个item设置缩进和check-box隐藏
      recursion (arr, i) {
        arr.forEach(e => {
          e.tier = i
          e.status = false
          if (e.subOrgList.length > 0) {
            this.recursion(e.subOrgList, i + 1)
          }
        })
        return arr
      },
      createQuestion (type) {
        let question = {
          title: null,
          questionType: null,
          optionList: []
        }
        let defaultOptionCount = 0
        switch (type) {
          case 'RADIO':
          case 'MULTIPLE':
            defaultOptionCount = 3
            break
          case 'COMPLETION':
            defaultOptionCount = 0
            break
          default:
            defaultOptionCount = 0
            break
        }
        for (let i = 0; i < defaultOptionCount; i++) {
          question.optionList.push('')
        }
        question.questionType = type
        return question
      },
      async doDeleteQuestion (idx) {
        if (this.submit.questionList.length === 1) return
        await this.$confirm('确认删除该题目? 删除后无法恢复', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        this.submit.questionList.splice(idx, 1)
      },
      // 添加问题
      doAddQuestion (type) {
        let option = this.createQuestion(type)
        this.submit.questionList.push(option)
      },
      // 添加选项
      doAddOption (questionIdx) {
        this.submit.questionList[questionIdx].optionList.push('')
      },
      // 删除选项
      doDeleteOption (questionIdx, optionIdx) {
        this.submit.questionList[questionIdx].optionList.splice(optionIdx, 1)
      },
      validQuestionTitle () {
        return this.submit.questionList.every((item, idx) => {
          if (!item.title) {
            this.$message.warning(`请填写题目${idx + 1}的题目内容`)
            return false
          }
          return true
        })
      },
      validOption () {
        return this.submit.questionList.every((question, questionIdx) => {
          if (question.questionType === 'COMPLETION') return true

          return question.optionList.every((option, optionIdx) => {
            if (!option) {
              this.$message.warning(`请填写题目${questionIdx + 1}的${String.fromCharCode(optionIdx + 65)}选项`)
              return false
            }
            return true
          })
        })
      },
      async doSubmit () {
        let submit = this.submit
        if (!submit.title) {
          this.$message.warning('请输入标题')
          return
        } else if (!submit.content || submit.content === '<p><br></p>') {
          this.$message.warning('请输入正文内容')
          return
        } else if (!this.validQuestionTitle()) {
          return
        } else if (!this.validOption()) {
          return
        } else if (!submit.orgIdList.length) {
          this.$message.warning('请选择推送范围')
          return
        }
        let loading = this.$loading({
          target: '.card',
          text: '数据提交中....'
        })
        await this.$ajax({
          url: '/survey/addSurvey.do',
          query: this.submit
        })
        loading.close()
        this.$remind('发布')
        this.$router.back()
      }
    }
  }
</script>

